import {  Table,Button,Form,DatePicker,Select  } from 'antd';
import React from 'react'
import OwnFetch from '../../../api/OwnFetch'; //

import moment from 'moment';

export default class Charging extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
            dataSource: [],
            totalData:{},
			name: '', //
			page:1,
            pageSize:10,
			loading: false,
            time: [],
            way:"all",//
		}
	}


	//
	onRest = () => {
		this.setState({
            way:"all",
            time: [],
		},this.initLoadData)
    }
    
    payChange=(value)=>{
        this.setState({way:value})
    }

	componentWillMount() {
		this.initLoadData();
	}

	//
	initLoadData = () => {
		this.setState({loading: true})
        let {time,way } = this.state;
        let param = {};
		if (time.length == 2) {
            let bTime = time[0].format("YYYY-MM-DD");
			let eTime = time[1].format("YYYY-MM-DD");
			param.bTime = bTime;
			param.eTime = eTime;
        }  
        if(way != 'all'){
            param.way = way;  
        }   
		OwnFetch("charging_list",param)
			.then(res => {
				if(res && res.code == "200") {
					let {datas,count} = res.data;
					// console.info("aaaa",res.data)
 					this.setState({dataSource: datas,totalData:count})
				}
				this.setState({loading: false})
            })
	}

	//
	onSearch = () => {
	  this.initLoadData();
	}



	timeonChange = (dates, dateStrings) => {
        this.setState({ time: dates });
	}

	getChangeDate=(key)=>{
		let time = [];
		if(key == 1){
			time.push(moment());
			time.push(moment());
		}else if(key==2){	
			time.push(moment().subtract(1, 'd'));
			time.push(moment());
		}else if(key==3){	
			time.push(moment().startOf('month'));
			time.push(moment());
		}else if(key== 4){	
			// let mont = moment().subtract(1, 'M')
			time.push( moment().subtract(1, 'M').startOf('month'));
			time.push( moment().subtract(1, 'M').endOf('month'));
		}
		this.setState({time},this.initLoadData)
	}
	

	columns = [{
		title: 'id',
		dataIndex: 'id',
	}, {
		title: '',
		dataIndex: 'recharge_time',
	}, {
		title: '',
		dataIndex: 'valid_time',
	}, {
		title: '',
		dataIndex: 'roleText',
	}, {
		title: 'id',
		dataIndex: 'invite_id',
	},{
		title: '',
		dataIndex: 'name',
	},{
		title: '',
		dataIndex: 'trade_type',
		render:(text, record, index) => text =="WX"?'':''
	},{
		title: '()',
		dataIndex: 'total',
		render:(text, record, index) => Number(text / 100)
	}]
	
	//
	getTotal=(value)=>{
		let style = {  color: 'red',marginLeft: '5px'}
		if(!value){
			return <span style={style}>0 </span>;
		}
		return <span  style={style}>{Number(value / 100) +''}</span>;
	}
    
	render() {
		//
		const FormItem = Form.Item;
        const { RangePicker } = DatePicker;
		const Option = Select.Option;
		let totalData = this.state.totalData;


		return(
			<div className="new_div_context">   
			  	<div style={{padding:"10px"}}> 
				  <Button style={{marginRight:15}} onClick={()=>this.getChangeDate(1)}></Button>
				  <Button style={{marginRight:15}} onClick={()=>this.getChangeDate(2)}></Button>
				  <Button style={{marginRight:15}} onClick={()=>this.getChangeDate(3)}></Button>
				  <Button style={{marginRight:15}} onClick={()=>this.getChangeDate(4)}></Button>  
				  </div>
				 <Form layout="inline" style={{marginLeft:10}} >
			
					<FormItem label="">
                        <RangePicker
                            // showTime={{ format: 'HH:mm:ss' }}
                            value={this.state.time}
                            onChange={this.timeonChange}
                            format="YYYY-MM-DD"
                        />
                    </FormItem>

                <FormItem label="">
                           <Select
							style={{ width: 200 }}  
							value={this.state.way}                     
                            onChange={this.payChange} >
                            <Option key="all"></Option>
                            <Option key="WX"></Option>
                            <Option key="ALI"></Option>
                        </Select>
                    </FormItem>

					<FormItem >				
						<Button  type="primary" icon="search" onClick={this.onSearch}></Button>	
						 <Button type="Default"  icon="reload" onClick={this.onRest} style={{ marginLeft: '10px' }}  ></Button>	
                    </FormItem>

					<FormItem  style={{ float: 'right', marginLeft: '20px' }}>	
				</FormItem>
			</Form>

        <div >
		<div style={{  fontSize: '18px',margin: '15px 0px'}}>
			<span style={{margin: '18px'}}>:{this.getTotal(totalData.total)}</span>
			<span style={{margin: '18px'}}>:{this.getTotal(totalData.r1)}</span>	
			<span style={{margin: '18px'}}>:{this.getTotal(totalData.r2) }</span>		
			<span style={{margin: '18px'}}>:{this.getTotal(totalData.r3)}</span>	
			<span style={{margin: '18px'}}>:{this.getTotal(totalData.r4)}</span>	
			<span style={{margin: '18px'}}>:{this.getTotal(totalData.r5)}</span>	
		</div>
          <Table
		    size="small"
            rowKey={(record,index) => index}
			dataSource={this.state.dataSource}
			columns={this.columns}
            loading={this.state.loading}
			pagination={{
				// current: this.state.page,
				pageSize:10,
				// total: this.state.total,
				showTotal: (total, range) => `${range[0]}-${range[1]} ${total}`,
				// showQuickJumper: true,
				// onChange: this.pageChange,
			}}
          />
        </div>
      </div>
		)
	}

}


