import React from 'react'
import { Upload, Form, Modal, InputNumber, Input, Icon } from 'antd'

import OwnFetch from '../../api/OwnFetch';//
import UploadFile from './UploadFile';//

const FormItem = Form.Item;

@Form.create()
export default class Add extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            visible: true,
            // imgurl:'',//
            previewVisible: false,
            previewImage: '',
            fileList: [],
            inputLoading: false
        }
    }

    componentDidMount() {
        //
        // console.info("dfsfsd",this.props.editData.imgurl)
        if (this.props.editData.imgurl) {
            let fileList = [];
            fileList.push({ uid: -1, status: 'done', url: this.props.editData.imgurl });
            this.setState({ fileList });
        }

    }


    handlePreview = (file) => {
        // console.info(file)
        this.setState({
            previewImage: file.url || file.thumbUrl,
            previewVisible: true,
        });
    }


    handleChange = ({ file, fileList, event }) => {
        this.setState({ fileList })
    }


    handleCancel = () => this.setState({ previewVisible: false })



    beforeUpload = (file) => {
        const isJPG = file.type === 'image/jpeg';
        const isGIF = file.type === 'image/gif';
        const isPNG = file.type === 'image/png';
        if (!isJPG && !isGIF && !isPNG) {
            Modal.error({
                content: 'JPG/PNG/GIF',
            });
            return false;
        }
        const isLt2M = file.size / 1024 / 1024 < 10;

        if (!isLt2M) {
            Modal.error({
                content: ' 10M!',
            });
            return false;
        }
        // this.setState({imageName:file.name,imageFile:file})
        return (isJPG || isGIF || isPNG) && isLt2M;
    }




    //
    handleCreate = () => {

        const { editData, refresh, closePage } = this.props;
        const { fileList } = this.state;
        // console.info("sfsdfs",this.state.fileList)

        this.props.form.validateFields((err, values) => {
            if (err) {
                return;
            }

            //
            if (editData.imgurl && fileList.length != 0) {
                values.imgurl = editData.imgurl;
            }

            //
            if (fileList.length == 1) {
                if (fileList[0].percent == 100) {
                    values.imgurl = fileList[0].response;
                }
            }

            //
            if (editData.id) {
                values.id = editData.id;
                OwnFetch("welcome_update", values)
                    .then(res => {
                        if (res && res.code == '200') {
                            this.props.form.resetFields();
                            refresh()
                            closePage();
                            //    

                        }
                    })
            } else {  // 
                OwnFetch("welcome_add", values)
                    .then(res => {
                        if (res && res.code == '200') {
                            this.props.form.resetFields();
                            refresh()
                            closePage();
                        }
                    })
            }
        });


    }


    onClearFrom = () => {
        this.props.form.resetFields();//
        this.props.closePage();
    }


    //
    setLoading = (loading) => {
        this.setState({ inputLoading: loading })
    }




    render() {
        const { getFieldDecorator } = this.props.form;


        const { editData } = this.props;

        let formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 6 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 14 },
            },
        };

        const { previewVisible, previewImage, fileList } = this.state;

        const uploadButton = (
            <div>
                <Icon type="plus" />
                <div className="ant-upload-text">Upload</div>
            </div>
        );

        return (<Modal
            width={"75%"}
            maskClosable={false}
            visible={this.state.visible}
            title={editData.id ? '' : ''}
            onCancel={this.onClearFrom}
            onOk={this.handleCreate}
        >
            <Form >
                <FormItem label="" {...formItemLayout} >
                    <div className="clearfix">
                        <Upload
                            action={OwnFetch.preurl + "/upload/image"}
                            listType="picture-card"
                            fileList={fileList}
                            // data={fileList}
                            onPreview={this.handlePreview}
                            onChange={this.handleChange}
                            beforeUpload={this.beforeUpload}
                        >
                            {fileList.length == 1 ? null : uploadButton}
                        </Upload>
                        <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
                            <img style={{ width: '100%' }} src={previewImage} />
                        </Modal>
                    </div>
                </FormItem>

                {/* <FormItem label="" {...formItemLayout} >             
                        <UploadFile getUrl={editData.url} setLoading={this.setLoading} />       
                </FormItem> */}

                <FormItem label="" {...formItemLayout} hasFeedback>
                    {getFieldDecorator('url', {
                        initialValue: editData.url,
                        rules: [{
                            required: true, message: '!'
                        }]
                    }
                    )(
                        <Input placeholder="" disabled={this.state.inputLoading} />
                    )}
                </FormItem>

                <FormItem label="" {...formItemLayout} >
                    {getFieldDecorator('px', {
                        initialValue: editData.px || 1,
                        rules: [{
                            required: true, message: '!'
                        }]
                    }
                    )(
                        <InputNumber min={1} max={99999} />
                    )}
                </FormItem>





            </Form>

        </Modal>)
    }
}


