import React from 'react'
import {Icon,Badge, Popover, List, Tabs, Tag } from 'antd'

// 
import { Scrollbars } from 'react-custom-scrollbars'

import styles from './index.css';

const TabPane = Tabs.TabPane;

export default class NoticeIcon extends React.Component {
    render() {
        const listData = [
            {
                id: '000000009',
                title: '',
                description: ' 2017-01-12 20:00 ',
                extra: '',
                status: 'todo',
                type: '',
            },
            {
                id: '000000010',
                title: '',
                description: ' 2017-01-06 2017-01-07 ',
                extra: '',
                status: 'urgent',
                type: '',
            },
            {
                id: '000000011',
                title: '',
                description: ' 2017-01-09 ',
                extra: ' 8 ',
                status: 'doing',
                type: '',
            },
            {
                id: '000000012',
                title: 'ABCD ',
                description: ' 2017-01-06 2017-01-07 ',
                extra: '',
                status: 'processing',
                type: '',
            }
        ].map(item => {
            if (item.extra && item.status) {
                const color = ({
                    todo: '',
                    processing: 'blue',
                    urgent: 'red',
                    doing: 'gold',
                })[item.status];
                item.extra = <Tag color={color} style={{ marginRight: 0 }}>{item.extra}</Tag>;
            }
            return item;
        });

        const messageContent = (
            <div>
                <Tabs defaultActiveKey="1" className={styles.tabs}>
                    <TabPane tab="(3)" key="1">
                        <Scrollbars style={{ height: 400 }}>
                            <List
                                className={styles.list}
                                dataSource={listData}
                                renderItem={item => (
                                    <List.Item className={styles.item}>
                                        <List.Item.Meta
                                            className={styles.meta}
                                            title={
                                                <div className={styles.title}>
                                                    {item.title}
                                                    <div className={styles.extra}>{item.extra}</div>
                                                </div>
                                            }
                                            description={
                                                <div>
                                                    <div className={styles.description} title={item.description}>
                                                        {item.description}
                                                    </div>
                                                </div>
                                            }
                                        />
                                    </List.Item>
                                )}
                            />
                            <div className={styles.all}></div>
                        </Scrollbars>
                    </TabPane>
                    <TabPane tab="(0)" key="2">
                        <Scrollbars style={{ height: 400 }}>
                        </Scrollbars>
                    </TabPane>
                    <TabPane tab="(0)" key="3">
                        <Scrollbars style={{ height: 400 }}>
                        </Scrollbars>
                    </TabPane>
                </Tabs>
            </div>
        );

        return (
            <Popover placement="bottomRight" content={messageContent} overlayClassName={styles.popover} trigger="click">
                <span className='noticeButtonClass'>
                    <Badge count={12}>
                        <Icon type="bell" className='noticeIcon'/>
                    </Badge>
                </span>
            </Popover>
        )
    }
}


