import { Modal, Form, Input, Select, InputNumber } from 'antd'
import React from 'react'
import OwnFetch from '../../api/OwnFetch';//
import { decode } from '../../../utils/util'; //
import Upload from '../../common/CommonUpload'

@Form.create()
export default class AddUserPageFrom extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            confirmDirty: false,
            visible: true,
            users: [],
        }
    }

   

    componentDidMount() {
      this.getUser();
    }

    getUser=()=>{
        OwnFetch("system_users").then(res=>{
            if(res && res.code==200){
                this.setState({users:res.data})
            }
        })
    }
    //
    handleCreate = () => {
        const { editData } = this.props;
        this.props.form.validateFields((err, values) => {
            if (err) {
                return;
            }
            // 
            if (editData.id == undefined) {
                // let str= Md5("abcdef");
                // console.info("13123131",str)  
                //
                values.password = decode(values.password);
                OwnFetch("system_add", values)
                    .then(res => {
                        if (res && res.code == '200') {
                            this.onClearFrom();
                            this.props.refresh();
                        }
                    })
            } else {  //
                values.id = editData.id;
                OwnFetch("system_edit", values)
                    .then(res => {
                        if (res && res.code == '200') {
                            this.onClearFrom();
                            this.props.refresh();
                        }
                    })
            }

        });
    }


    //
    handleConfirmBlur = (e) => {
        let value = e.target.value;
        this.setState({ confirmDirty: this.state.confirmDirty || !!value });
    }

    //
    checkPassword = (rule, value, callback) => {
        if (value && value !== this.props.form.getFieldValue('password')) {
            callback('');
        } else {
            callback();
        }
    }

    //
    checkConfirm = (rule, value, callback) => {
        if (value && this.state.confirmDirty) {
            this.props.form.validateFields(['confirm'], { force: true });
        }
        if (value && value.length < 8) {
            callback('8');
        } else {
            callback();
        }

    }

    // //
    // checkEmail = (rule, value, callback) =>{
    // var re = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.|\-]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
    //     if (value && !re.test(value)) {
    //     callback('');
    //     } else {
    //     callback();
    //     }
    // }

    // //
    // checkPhone = (rule, value, callback) =>{
    // var re = /^1[34578]\d{9}$/;
    //     if (value && !re.test(value)) {
    //     callback('');
    //     } else {
    //     callback();
    //     }
    // }

    onClearFrom = () => {
        this.props.form.resetFields();//
        this.props.closePage();
    }




    render() {
        const { editData } = this.props;
        const { getFieldDecorator } = this.props.form;
        const FormItem = Form.Item;
        const Option = Select.Option;

        let formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 6 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 14 },
            },
        };


        return (
            <Modal
                width={600}
                maskClosable={false}
                visible={true}
                title={editData.yhxm == undefined ? '' : ''}
                onCancel={this.onClearFrom}
                onOk={this.handleCreate}
            >
                <Form >
                    <FormItem label="" {...formItemLayout} hasFeedback>
                        {getFieldDecorator('username', {
                            initialValue: editData.username,
                            rules: [{
                                required: true, message: '!'
                            }]
                        }
                        )(
                            <Input placeholder="" disabled={editData.id != undefined} />
                        )}
                    </FormItem>

                    {editData.id == undefined && <div><FormItem label="" {...formItemLayout} hasFeedback>
                        {getFieldDecorator('password', {
                            rules: [{ required: true, message: '!', },
                            { validator: this.checkConfirm }]
                        }
                        )(
                            <Input type="password" />
                        )}
                    </FormItem>

                        <FormItem {...formItemLayout} label="" hasFeedback >
                            {getFieldDecorator('confirm', {
                                rules: [{
                                    required: true, message: '!',
                                }, {
                                    validator: this.checkPassword,
                                }],
                            })(
                                <Input type="password" onBlur={this.handleConfirmBlur} />
                            )}
                        </FormItem>     </div>}


                    <FormItem label="" {...formItemLayout} hasFeedback >
                        {getFieldDecorator('user_id', {
                            initialValue: editData.user_id,
                            rules: [{
                                required: true, message: '!'
                            }]
                        }
                        )( <Select >
                            {this.state.users.map((item) => {
                            return <Option key={item.key}>{item.value}</Option>
                            })}
                        </Select>
                        )}
                    </FormItem>

                    <FormItem label="()" {...formItemLayout} >
                        {getFieldDecorator('total', {
                            initialValue: editData.total,
                            rules: [{
                                required: true, message: '!'
                            }]
                        }
                        )(
                            <InputNumber min={0} />
                        )}
                    </FormItem>

                    <FormItem label="" {...formItemLayout} >
                        {getFieldDecorator('people', {
                            initialValue: editData.people,
                            rules: [{
                                required: true, message: '!'
                            }]
                        }
                        )(
                            <InputNumber max={99999} min={0} />
                        )}
                    </FormItem>

                    <FormItem label="apk" {...formItemLayout} >
                        {getFieldDecorator('fileId', {
                            initialValue: editData.fileId,
                        }
                        )(
                            <Upload />
                        )}
                    </FormItem>


                    <FormItem label="" {...formItemLayout} >
                        {getFieldDecorator('remark', {
                            initialValue: editData.remark
                        })(
                            <Input.TextArea rows={4} placeholder="" />
                        )}
                    </FormItem>

                </Form>

            </Modal>

        )
    }

}




